<template>
  <section class="login-wrapper">
    <div class="main-wrapper">
      <div class="main-content">
        <div class="top-wrapper">
          <div class="img">
            <img height="70" :src="info.logo"/><span class="split"></span> <span>业务支持系统管理后台</span>
          </div>
        </div>
        <div class="center-wrapper">
          <transition :name="`${transitionName}-fade`" mode="out-in">
            <component @action="action" :mobile="mobile" :is="view"></component>
          </transition>
        </div>
      </div>
    </div>
    <el-footer class="footer-wrapper" :data="info" :className="'login-footer'"></el-footer>
  </section>
</template>

<script>
  import ElFooter from '../layout/footer.vue'
  import LoginTpl from './template/login-tpl.vue'
  import ForgetTpl from './template/forget-tpl.vue'
  import SetWordTpl from './template/set-word-tpl.vue'
  import Bus from '@/bus'
  export default {
    data () {
      return {
        view: 'login',
        transitionName: 'in',
        mobile: '',
        token: this.$localStorage.get(this.$const.COOKIE.KEY_TOKEN),
        info: {}
      }
    },
    components: {
      ElFooter,
      'login': LoginTpl,
      'forget': ForgetTpl,
      'nextStep': SetWordTpl
    },
    methods: {
      action (type, value) {
        switch (type) {
          case 'login':
            this.view = 'login'
            this.transitionName = 'out'
            break
          case 'forget':
            this.view = 'forget'
            this.transitionName = 'in'
            break
          case 'nextStep':
            this.mobile = value
            this.view = 'nextStep'
            this.transitionName = 'in'
            break
        }
      },
      init () {
        this.$store.dispatch('WBS_NS', {
          name: 'ns.enterpriseInfo',
          data: {}
        }).then(res => {
          if (res.code === 0) {
            this.info = res.value
            this.$localStorage.set('logo', this.info.logoThumbnail)
            Bus.$emit('showLogo')
          }
        })
      }
    },
    mounted () {
      this.init()
    }
  }
</script>

