<template>
  <el-row>
    <el-col class="login-wrappers">
      <div class="input-title">Hi,欢迎登录</div>
      <div class="input-box">
        <el-form :model="loginModel" ref="login" :rules="loginRules">
          <nb-form
            v-model="loginModel.phone"
            placeholder="输入登录账号"
            :maxs="11"
            input="isPhone"
          ></nb-form>
          <nb-form
            v-model="loginModel.password"
            placeholder="输入密码"
            type="password"
            required=":输入密码"
          >
            <el-row slot="in" @click.native="forget" class="right-box box1">
              忘记密码？
            </el-row>
          </nb-form>
          <nb-form
            v-if="code"
            placeholder="验证码"
            required=":输入验证码"
            v-model="captcha"
          >
            <el-row slot="in" @click.native="getCodeImg" class="right-box box2" :style="{backgroundImage:`url(${code})`}"></el-row>
          </nb-form>
        </el-form>

      </div>
      <div class="btn-box">
        <el-button class="confirm-btn" type="primary" @click="confirmLogin">登录</el-button>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import md5 from 'md5'
import NbForm from '@/components/nb-form'
import isEqual from 'lodash/isEqual'
import {
  getQueryParams
} from '@/common/utils'
export default {
  data () {
    return {
      code: '',
      captcha: '',
      loginModel: {
        phone: '',
        password: ''
      },
      loginRules: {
        phone: [
          {required: true, trigger: 'blur', message: '请输入手机号'}
        ],
        password: [
          {required: true, trigger: 'blur', message: '请输入密码'}
        ]
      },
      loading: null
    }
  },
  components: {
    NbForm
  },
  methods: {
    forget () {
      this.$emit('action', 'forget')
    },
    confirmLogin () {
      let cloneData = {
        param: {
          account: this.loginModel.phone,
          password: md5(this.loginModel.password),
          returnUrl: getQueryParams('returnUrl')
        }
      }
      this.code && (cloneData.param.captcha = this.captcha)
      this.$store.dispatch('WBS_NS', {
        name: 'passport.login.security',
        data: cloneData.param
      }).then(res => {
        if (res.code === 0) {
          this.$message.success('登录成功！')
          setTimeout(() => {
            window.location.href = res.value
          }, 1000)
        } else if (res.errorCode === 1004) {
          this.$message.warning(res.msg)
          setTimeout(() => {
            this.$router.push({name: 'reset'})
          }, 1000)
        } else {
          if (res.value) {
            this.code = `data:image/png;base64,${res.value.captcha}`
          }
          this.$message.warning(res.msg)
        }
      })
    },
    getCodeImg () {},
    keydownHandel (e) {
      if (isEqual(e.keyCode, 13)) {
        this.confirmLogin()
      }
    }
  },
  mounted () {
    document.addEventListener('keydown', this.keydownHandel)
  },
  beforeDestroy () {
    document.removeEventListener('keydown', this.keydownHandel)
  }
}
</script>

